import {MATERIAL} from "../objects/models";
import * as dat from 'dat.gui';

/**
 * @type {GUI}
 */
const GUI = new dat.GUI({
    width: 600,
});


let frequency = GUI.addFolder('');

frequency.add(
    MATERIAL.uniforms.uFrequency.value,
    'x', 0, 20, 0.01
).name("     X");

frequency.add(
    MATERIAL.uniforms.uFrequency.value,
    'y', 0, 20, 0.01
).name("     Y");

export {GUI};